body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin: 50px auto;
  padding: 30px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #dddddd solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.form-group {
  width: 100%;
  margin-bottom: 20px;
}

h5 {
  margin: 10px 0;
}

.searchBar {
  width: 100%;
  align-self: auto;
}

.bookDiv {
  display: flex;
  flex-direction: column;
  padding: 10px 5px;
}
.top {
  display: flex;
  justify-content: space-between;
}
.buttons {
  color: #2a878c;
  display: flex;
  justify-content: flex-end;
  margin-top: -10px !important;
  padding-top: 0 !important;
}

.bottom {
  display: flex;
}
img {
  margin-top: 5px;
  margin-right: 15px;
}

h5 {
  margin: 10px 0;
}
a {
  color: #2a878c;
}

.delete-btn {
  float: right;
  color: #e74944;
  padding-top: 2px;
  font-size: 1.3em;
}


.hero {
  padding-bottom: 60px;
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
h1 {
  margin-bottom: 30px;
}

.navbar {
  margin-bottom: 50px !important;
  border-bottom: 1px solid black;
}

